.element {
  width: 100%;
  position: relative;
  margin-right: 5px;
}
.element:hover {
  -webkit-box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.overlay1 {
  position: absolute;
  height: 0px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(80, 90, 95, 0.5);
  overflow: hidden;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.overlay1 h1,
h6 {
  color: #fff;
  margin-bottom: 0px;
}
.overlay1 h2 {
  color: white;
}
.price {
  -ms-flex-item-align: end;
      align-self: flex-end;
  height: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.detail {
  padding: 10px;
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.element:hover .overlay1 {
  height: 40%;
}



