.signup{
    width:25%;
    border-radius: 20px;
    margin:auto;
    background-color:#e3e3e3;
    height:80vh;
    -webkit-box-shadow: 0px 5px 5px 5px;
            box-shadow: 0px 5px 5px 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: absolute;
    position: absolute;
    z-index: 10;
    top:10%;
    left:40%;
    -webkit-animation-name: examplesignup;
            animation-name: examplesignup;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;

}
.outersofsignup{
    width:90%;
    margin: auto;
    height: 95%;
    background-color: #FFF;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border-radius: 20px;

}
@-webkit-keyframes examplesignup {
    0%   { top:-500px;}
   
    100%{top:10%}
}
@keyframes examplesignup {
    0%   { top:-500px;}
   
    100%{top:10%}
}



