.overview{
    width: 100%;
    margin:auto;
 padding: 20px;
 display:-ms-flexbox;
 display:flex;
 height: 83vh;
 -ms-flex-pack: space-evenly;
     justify-content: space-evenly;
}
.sider{
    width:25%;
    height:74%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-direction: column;
        flex-direction: column;
   
}
.sellername{
    display: -ms-flexbox;
    display: flex;
    background-color: #fff;
    padding: 20px;
}
.displaydata{
    width:60%;
    background-color: #fff;
    padding: 20px;
}
.sides{
    background-color: #fff;
    padding: 8px;
    text-align: left;

}
.sides h3{
    margin:7px;
}
@media only screen and (max-width: 600px) {
    .sider{
        display: none
    }
    .displaydata{
        width:100%;
    }
}


