.head1 {
  width: 100%;
  padding: 5px;
  font-size: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
}
.headWrap {
  background-color: #fff;
}
.head1 div {
  -ms-flex-item-align: center;
      align-self: center;
}
.head2 {
  width: 100%;
  color: white;
  padding: 5px;
}
.head3 {
  width: 90%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.head22 {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  color: #fff;
}
.head21 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #fff;
}
.select {
  width: 20%;
  margin: 4px;
}
.search {
  width: 58%;
  margin: 3px;
}

.arrow-anim {
  margin-right: 15px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.arrow-anim:hover {
  margin-right: 20px;
}
@media only screen and (max-width: 600px) {
  .head3 {
    width: 100%;
  }
  .select {
    width: 60%;
    font-size: 100%;
  }
  .search {
    width: 50%;
  }
}



